        name: "description",
        content:
          "Do you have any enquires? Send a message now to Lydia Rodriguez",
      },
      {
        hid: "og:title",
        name: "og:title",
        content: "Kontakt ðŸ“§ - Lydia Rodriguez",
      },
      {
        property: "og:description",
        content:
          "Do you have any enquires? Send a message now to Lydia Rodriguez",
      },
      {
        hid: "og:image",
        name: "og:image",
        content: require("@/assets/contact.jpg"),
      },
    ],
  },
};
